.toggle__container {
    display: flex;
    justify-content: center;
    padding-bottom: 40px;
    border-bottom: 1px solid rgb(66, 66, 66);
    margin-bottom: 98px;
}

.toggle-button {
    position: relative;
    display: inline-block;
    width: 36px;
    height: 20px;
    margin: 0;
    vertical-align: top;
    background: #ffffff;
    border: none;
    border-radius: 20px;
    outline: none;
    cursor: pointer;
    -webkit-appearance: none;
    -moz-appearance: none;
    appearance: none;
    transition: 0.3s;
    top: 1px;
    bottom: 1px;
}

.toggle-button::after {
    content: "";
  
    display: inline-block;
    position: absolute;

    top: 2px;
    bottom: 2px;
    left:2px;
  
    width: 16px;
    height: 16px;
    background-color: rgb(61, 220, 132);
    border-radius: 50%;
  
    transform: translateX(0);
    transition: 0.3s;
  }

  .toggle-button:checked::after {
    transform: translateX(calc(100%));
    background-color: #fff;  
  }
  .toggle-button:checked {
    background-color: rgb(61, 220, 132);
  } 
.toggle-button__label {
    margin-left: 12px;
    font-size: 12px;
    line-height: 23px;
    font-weight: 400;
    color: rgb(255, 255, 255);
}

@media screen and (max-width: 767px){  
  .toggle__container {
      margin-bottom: 42px;
  }
}

